<template>
    <div id="app">
        <br><br>
        <a href="https://github.com/alziqziq/vue-paginate-al" target="_blank">https://github.com/alziqziq/vue-paginate-al</a>

        <h1>
            <b>
                Vue Paginate Al
            </b>
        </h1>
        <h3>
            Check Console :
        </h3>

        <hr>

        <br><br><br>
        <p>
            <b>Default : </b>
        </p>
        <vue-paginate-al :totalPage="10" @btnClick="goToFunction"></vue-paginate-al>
        <p>{{msg1}}</p>

        <hr>

        <br><br><br>
        <p>
            <b>Customize Background Color  : </b>
        </p>
        <vue-paginate-al :totalPage="15" customActiveBGColor="#5ccb28" :withNextPrev="false"></vue-paginate-al>
        <p>{{msg2}}</p>

        <hr>

        <br><br><br>
        <p>
            <b>Current Page : </b>
        </p>
        <vue-paginate-al :totalPage="20" :currentPage="3"></vue-paginate-al>
        <p>{{msg5}}</p>

        <hr>

        <br><br><br>
        <p>
            <b>Return Data : </b>
        </p>
        <vue-paginate-al :totalPage="20" customActiveBGColor="pink" :myData="'myname'" @btnClick="goToFuncWithData"></vue-paginate-al>
        <p>{{msg3}}</p>


        <hr>

        <br><br><br>
        <p>
            <b>
                Return Multiple Data :
            </b>
        </p>
        <vue-paginate-al :totalPage="20" :myData="['myname', 'myemail']" @btnClick="goToFuncWithMultipleData"></vue-paginate-al>
        <p>{{msg4}}</p>

        <br><br><br>


    </div>
</template>

<script>
    export default {
        name: 'app',
        data : function()
        {
            return {
                msg1 : '<vue-paginate-al :totalPage="10" @btnClick="goToFunction"></vue-paginate-al>',
                msg2 : '<vue-paginate-al :totalPage="15" customActiveBGColor="#5ccb28" :withNextPrev="false"></vue-paginate-al>',
                msg5 : '<vue-paginate-al :totalPage="20" :currentPage="3"></vue-paginate-al>',
                msg3 : '<vue-paginate-al :totalPage="20" customActiveBGColor="pink" :myData="\'myname\'" @btnClick="goToFuncWithData"></vue-paginate-al>',
                msg4 : '<vue-paginate-al :totalPage="20" :myData="[\'myname\', \'myemail\']" @btnClick="goToFuncWithMultipleData"></vue-paginate-al>'
            }
        },
        methods :
        {
            goToFunction : function(n)
            {
                console.log(n);
            },

            goToFuncWithData : function(n, data)
            {
                console.log('n : ', n);
                console.log('data : ', data);
            },

            goToFuncWithMultipleData : function(n, data)
            {
                console.log('n : ', n);
                console.log('data 1 : ', data[0]);
                console.log('data 2 : ', data[1]);
            }
        }
    }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
</style>



